<template>
  <div id="app">
    <h1>Rust RaidableBases Loot Editor</h1>
    <Row type="flex" justify="center">
      <Col :span="4">
        <Upload :before-upload="handleBeforeUpload" action="" :format="['json']">
            <Button icon="ios-document-outline" size="small" type="primary">Open Loot File</Button>
        </Upload>
      </Col>
      <Col :span="4">
        <Button icon="ios-download-outline" type="primary" @click="SaveLoot" size="small">Save Loot File</Button>
      </Col>
    </Row>
    <Tabs v-model="categoryFilter">
        <TabPane label="all" name="all" key="category_all"></TabPane>
        <TabPane :label="v" :name="v" v-for="(v, k) in categories" :key="'category_' + k"></TabPane>
    </Tabs>
    <RadioGroup v-model="rarityFilter" type="button" size="small" style="margin-bottom: 20px;">
        <Radio label="all"></Radio>
        <Radio :label="v" v-for="(v,k) in rarity" :key="'rarity_' + k"></Radio>
    </RadioGroup>
    <Row v-if="items">
      <Col span="3" v-for="(item, k) in items" :key="k" v-show="(categoryFilter === 'all' || categoryFilter === item['Category']) && (rarityFilter === 'all' || rarityFilter === item['rarity'])">
        <Card style="width: 100%;height: 200px;">
          <div style="text-align: center">
            <h4>{{ item.Category }}</h4>
            <img :src="'/images/' + item.shortname + '.png'" style="width: 64px; height: 64px;"/>
            <h4>({{ item.rarity }})</h4>
            <h3>{{ item.Name }}</h3>
            <Row>
              <Col span="12"><InputNumber v-model="item.amountMin" :min="0" size="small"/></Col>
              <Col span="12"><InputNumber v-model="item.amount" :min="0" size="small"/></Col>
            </Row>
          </div>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import db from './assets/db.json'
export default {
  name: 'app',
  data () {
    return {
      items: db,
      rarityFilter: 'all',
      categoryFilter: 'all',
      difficults: ['Easy', 'Medium', 'Hard', 'Expert', 'Nightmare'],
      rarity: ['Common', 'Uncommon', 'Rare', 'VeryRare', 'None'],
      categories: ['Attire', 'Tool', 'Resources', 'Electrical', 'Construction', 'Weapon', 'Items', 'Traps', 'Ammunition', 'Misc', 'Medical', 'Component', 'Food', 'Fun']
    }
  },
  created() {
    for(let item in this.items){
      this.items[item].amount = 0 // this.items[item].stackable
      this.items[item].amountMin = 0
    }
  },
  methods: {
    handleBeforeUpload(file) {
      let reader = new FileReader()
      reader.readAsText(file)
      reader.onload = (e) => {
        let loots = JSON.parse(e.target.result)
        for(let i in loots) {
          let loot = loots[i]
          let lootItem = this.items.find( x => { return x.shortname === loot.shortname })
          lootItem.amount = loot.amount
          lootItem.amountMin = loot.amountMin
          this.$set(this.items, i, lootItem)
        }
      }
      return false
    },
    SaveLoot () {
      let loots = []
      for(let item of this.items) {
        if (item.amount === 0 && item.amountMin === 0)
          continue
        loots.push({
          shortname: item.shortname,
          amount: item.amount,
          skin: 0,
          amountMin: item.amountMin
        })
      }
      let saveData = JSON.stringify(loots, undefined, 4)
      let blob = new Blob([saveData], {
        type: 'application/json'
      })
      let fileName = Date.parse(new Date()) + '.json'
      if (window.navigator.msSaveOrOpenBlob) {
        navigator.msSaveBlob(blob)
      } else {
        let link = document.createElement('a')
        link.href = window.URL.createObjectURL(blob)
        link.download = fileName
        link.click()
        window.URL.revokeObjectURL(link.href)
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  margin-bottom: 20px;
}
</style>
